<template>
    <div class="order-item">

        <div class="ball-container">

            <div class="ball" v-for="ball in redBalls">
                {{ball}}
            </div>
            <div class="ball blue">{{blueBall}}</div>

        </div>

        <div class="cart-desc">
            <span>单式{{count}}注{{count * 2}}元</span>
        </div>

    </div>
</template>

<script>
    export default {
        name: "OrderItem",
        props: {
            data: {
                required: true,
                type: Object
            }
        },
        computed: {
            redBalls() {
                return this.data.red.split(",")
            },
            blueBall() {
                return this.data.blue;
            },
            count() {
                return this.data.count
            }
        }
    }
</script>

<style scoped>
    .order-item {
        width: 86%;
        margin-left: 7%;
        display: flex;
        flex-direction: column;
        border: .1rem none #33333333;
        border-bottom-style: solid;
        margin-top: .5rem;
    }

    .ball-container {
        width: 100%;
        margin-top: .8rem;
        display: flex;
        justify-content: space-between;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(229, 28, 35, 1);
        border-radius: 1.5rem;
        margin-right: .5rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.blue {
        color: rgba(0, 0, 255, 1);
        border: .12rem solid rgba(0, 0, 255, 1);
    }

    .cart-desc {
        margin: .5rem 0;
        font-size: 1.2rem;

    }

</style>