<template>

    <div class="root">

        <title-bar :title="'订单详情'" :back="true" @onBack="clickBack"></title-bar>

        <div class="content" v-if="data != null">

            <div class="order">
                <div>订单号:{{data.oid}}</div>

                <order-item v-for="item in data.orderItems" :data="item"></order-item>
            </div>

            <div class="pay-container">

                <div class="pay-item" @click="type = 0">
                    <span>黑马币支付</span>
                    <input type="radio" v-model="type" value="0"/>
                </div>
                <div class="pay-item" @click="type = 1">
                    <span>支付宝支付</span>
                    <input type="radio" v-model="type" value="1"/>
                </div>
                <div class="pay-item" @click="type = 2">
                    <span>微信支付</span>
                    <input type="radio" v-model="type" value="2"/>
                </div>

            </div>

            <div class="btn-pay">
                确认付款{{money}}元
            </div>

        </div>

    </div>

</template>

<script>
    import TitleBar from "../components/TitleBar";
    import OrderItem from "../components/OrderItem";

    export default {
        name: "OrderDetailPage",
        components: {OrderItem, TitleBar},
        data() {
            return {
                data: null,
                type: 0
            }
        },
        mounted() {
            this.data = this.$route.params.data
        },
        computed: {
            money() {
                if (this.data == null || this.data == undefined) return 0

                let money = 0
                this.data.orderItems.forEach(function (item) {
                    money += item.count * 2
                })
                return money
            }
        },
        methods: {
            clickBack() {
                this.$router.go(-1)
                // this.$router.push({path: "/shopping"})
            }
        }
    }
</script>

<style scoped>

    .content {
        display: flex;
        flex-direction: column;
    }

    .order {
        width: 96%;
        height: 100%;
        margin-left: 2%;
    }

    .pay-item {
        height: 3.2rem;
        border: .1rem none #33333333;
        border-top-style: solid;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .pay-item:active, .btn-pay:active {
        background-color: #33333333;
        transition: all .4s;
    }

    .pay-item > span {
        width: 40%;
        text-align: center;
        font-size: 1.4rem;
    }

    .pay-item > input {
        width: 30%;

    }

    .btn-pay {
        width: 100%;
        height: 5.5rem;
        border: .1rem none #33333333;
        border-top-style: solid;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.4rem;
    }

</style>